  max-height: 100px;
}
.App-body-container {
  padding-top: 30px;
  max-width: 90%;
  margin: 0 auto;
}

.SalaryStat-blocks {
  display: flex;
  justify-content: space-evenly;
  padding-top: 20px;
  padding-bottom: 20px;
}

.ShowDataButton {
  text-decoration: none;
  display: inline-block;
  width: 170px;
  height: 35px;
  line-height: 15px;
  border-radius: 5px;
  margin: 0px 10px;
  font-family: 'Montserrat', sans-serif;
  font-size: 10px;
  text-transform: uppercase;
  text-align: center;
  letter-spacing: 3px;
  font-weight: 600;
  color: #524f4e;
  background: white;
  box-shadow: 0 8px 15px rgba(0, 0, 0, 0.1);
  transition: 0.3s;

  opacity: 0; /*Элемент полностью прозрачный (невидимый)*/
  transition: 1s; /*Скорость перехода состояния элемента*/
  -webkit-animation: show 1s 1;
          animation: show 1s 1; /* Указываем название анимации, её время и количество повторов*/
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards; /* Чтобы элемент оставался в конечном состоянии анимации */
  -webkit-animation-delay: 0.5s;
          animation-delay: 0.5s; /* Задержка перед началом */
}

@-webkit-keyframes show {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes show {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.ShowDataButton:hover {
  background: #dde0e7;
  box-shadow: 0 15px 20px rgba(31, 123, 132, 0.4);
  color: black;
  -webkit-transform: translateY(-5px);
          transform: translateY(-5px);
}

